
.page-container {
  height: 100%;
  position: absolute;
  width: 100%;
}
.page-container .picker-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.page-container .picker-container {
  position: absolute;
  background-color: #fff;
  width: 100%;
  bottom: 0;
  z-index: 2;
}
.page-container .picker-container .toolbar {
  padding: 0 5%;
  height: 1.5rem;
  font-size: 0.76rem;
  color: #389cf2;
}
.page-container .addcar-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.page-container .addcar-dailog {
  position: absolute;
  background-color: #fff;
  width: 60%;
  margin: 0 20%;
  z-index: 2;
  height: 2.6rem;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  padding: 0.5rem 0;
}
.page-container .addcar-dailog .input-control input {
  border: none;
  font-size: 0.56rem;
  line-height: 2em;
  text-align: center;
  outline: none;
  width: 94%;
  border: 1px solid #aaa;
  margin: 0 3%;
}
.page-container .addcar-dailog .button-group {
  position: absolute;
  bottom: 0;
  height: 1.2rem;
  width: 100%;
}
.page-container .addcar-dailog .button-group .button {
  width: 49.8%;
  float: left;
  background-color: #00bffe;
  color: #fff;
  font-size: 0.56rem;
  height: 100%;
  text-align: center;
  line-height: 1rem;
}
.page-container .addcar-dailog .button-group .button + div {
  margin-left: 0.4%;
}
.page {
  height: 100%;
  position: absolute;
  width: 100%;
}
.page .page-container {
  height: 100%;
}
.page .page-container .page-content {
  background-color: #efefef;
  height: 100%;
  overflow: auto;
}
.page .page-container .page-content .car-list {
  font-size: 0.67rem;
}
.page .page-container .page-content .car-list .own-item {
  height: 2rem;
  background-color: #fff;
  border-bottom: 1px solid #efefef;
  padding: 0 3%;
}
.page .page-container .page-content .car-list .own-item .iconfont {
  font-size: 0.76rem;
  margin-right: 0.3rem;
}
.page .page-container .page-content .car-list .own-item .iconfont.active {
  color: #fc4c1d;
}
.page .page-container .page-content .car-list .own-item .car-series {
  margin-right: 0.3rem;
}
.page .page-container .page-content .car-list .own-item .car-plate {
  font-size: 0.57rem;
  color: #888;
  margin-right: 0.3rem;
}
.page .page-container .page-content .car-list .own-item .car-vin {
  font-size: 0.57rem;
  color: #888;
}
.page .page-container .page-content .car-list .add-car .default-show {
  height: 1.8rem;
  background-color: #fff;
  border-bottom: 1px solid #efefef;
  padding: 0 3%;
}
.page .page-container .page-content .car-list .add-car .default-show .iconfont {
  font-size: 0.76rem;
  margin-right: 0.3rem;
}
.page .page-container .page-content .car-list .add-car .default-show .iconfont.active {
  color: #fc4c1d;
}
.page .page-container .page-content .car-list .add-car .drop-down-form {
  padding: 0 10%;
  background-color: #fff;
}
.page .page-container .page-content .car-list .add-car .drop-down-form .input-control {
  position: relative;
  border-bottom: 1px solid #efefef;
}
.page .page-container .page-content .car-list .add-car .drop-down-form .input-control input {
  font-family: 'Microsoft Yahei';
  height: 1.8rem;
  line-height: 1.8rem;
  outline: none;
  border: none;
  background: transparent;
}
.page .page-container .page-content .car-list .add-car .drop-down-form .input-control .iconfont {
  position: absolute;
  right: 0rem;
  line-height: 1.8rem;
  font-size: 1rem;
  color: #888;
}
.page .page-container .page-content .car-list .add-car .drop-down-form .input-control .add-button {
  padding: 0.3rem 0.3rem;
  background-color: #fc4c1b;
  font-size: 0.51rem;
  color: #fff;
}
.page .page-container .button-control {
  background-color: #efefef;
  color: #fff;
  font-size: 0.77rem;
  overflow: hidden;
  border-radius: 3px;
}



