
.swiper {
  position: relative;
}
.swiper .mini-pagination {
  position: absolute;
  left: 0rem!important;
  bottom: -2.4rem;
  transform: translate3d(0, -50%, 0);
}
.swiper .mini-pagination .swiper-pagination-bullet {
  height: 0.2rem;
  width: 0.2rem;
}
.swiper .mini-pagination .swiper-pagination-bullet-active {
  background-color: #00bffe;
}



